import React, { Component } from "react";
import logo from "../logo.svg";
import { Link } from "react-router-dom";
import Footer from './Footer';
import Modal from './Modal';

class Label extends Component {
  constructor(props) {
    super(props);
    this.state = {
      releases: [],
      images: [],
      result: this.props.location.state,
      show: false,
      sublabels: [],
      sites: [],
      pagination: {},
      pages: Number,
      page: Number,
      start_index: 0,
      per_page: 0,
      visible: 25,
      toggleNum: 25,
      footer: 'secondary'
    };
  }

  showModal = e => {
    e.preventDefault();
    this.setState({ show: true });
  };

  hideModal = e => {
    e.preventDefault();
    this.setState({ show: false });
  };

  handlePrev = (e, id) => {
    e.preventDefault();
    const { per_page, page, visible, start_index } = this.state;

    this.setState((prev) => {
      return {
        visible: prev.visible - 25
      }
    }, () => {
      console.log('fetch previous data');
      if(visible <= per_page) {
        fetch(`https://api.discogs.com/labels/${id}/releases?per_page=${
          this.state.per_page + this.state.toggleNum}&page=${this.state.page - 1}&token=${process.env.REACT_APP_API_KEY}`)
        .then(res => res.json())
        .then(data => {
            this.setState({
              page: page - 1,
              per_page: per_page - 25,
              releases: data.releases
            }, () => console.log(this.state));
        });
      }
    });
  };

  handleLoad = (e, id) => {
    e.preventDefault();
    this.setState((prev) => {
      return {
        visible: prev.visible + 25
      }
    }, () => this.handleNext(id))
  }

  handleNext = (id) => {
    const { per_page, page, visible, start_index } = this.state; 
    
    if(visible >= per_page) {
      fetch(`https://api.discogs.com/labels/${id}/releases?per_page=${
      this.state.per_page + this.state.toggleNum}&page=${this.state.page + 1}&token=${process.env.REACT_APP_API_KEY}`)
      .then(res => res.json())
      .then(data => {
          this.setState({
            page: page + 1,
            per_page: per_page + 25,
            releases: data.releases
          }, () => console.log('state: ',this.state));
      });
    }
  };

  handleStyleChange = e => {
    console.log('handleStyleChange Ran');
  }

  handleShowNumChange = e => {
    console.log('handleChange Ran');
  }

  componentDidMount = () => {
    const { id } = this.props.match.params;

    fetch(`https://api.discogs.com/labels/${id}/releases?token=${process.env.REACT_APP_API_KEY}`)
      .then(res => res.json())
      .then(data => {
        this.setState(
          {
            pagination: data.pagination,
            page: data.pagination.page,
            pages: new Array(data.pagination.pages),
            per_page: data.pagination.per_page,
            releases: data.releases
          },
          () => console.log(this.state)
        );
      });

    fetch(`https://api.discogs.com/labels/${id}`)
      .then(res => res.json())
      .then(data => {
        this.setState({
          profile: data.profile,
          sublabels: data.sublabels,
          sites: data.urls
        });
      });
  };

  render() {
    let labelContent;
    const { id } = this.props.match.params;
    const { result } = this.props.location.state;
    const {
      profile,
      sublabels,
      sites,
      releases,
      pagination,
      start_index,
      visible,
      per_page,
      page,
      pages,
      images
    } = this.state;

    if (releases === undefined || releases.length === 0) {
      return <div className="loading-container">Loading...</div>;
    }

    labelContent = (
      <React.Fragment>
        <div className="container label">
          <div className="label-top">
            <div className="label-top-left">
              <div className="label-image">
                <a
                  href={`/sp/react/react-discogs-search/labels/${id}`}
                  onClick={this.showModal}
                >
                  <img
                    src={
                      result.type === "label"
                        ? result.cover_image
                        : "https://s.discogs.com/images/default-label.png"
                    }
                    alt="default-label"
                  />
                </a>
              </div>
              <div className="label-image-link">
                <a
                  href={`/sp/react/react-discogs-search/labels/${id}`}
                  onClick={this.showModal}
                >
                  More Images
                </a>
              </div>
            </div>
            <div className="label-top-center-label-desc">
              <div className="label-title">
                <p>{result.title}</p>
              </div>
              <div className="label-profile">Profile: {profile}</div>
              <div className="label-sublabels">
                <p>
                  Sublabels:
                  {sublabels.map(sublabel => " " + sublabel.name).join(", ")}
                </p>
              </div>
              <div className="label-sites">
                <p>
                  Sites:{" "}
                  <span className="sites">
                    {sites.map((site, index) => (
                      <a key={index} href={site}>
                        {site
                          .replace(/^(?:https?:\/\/)?(?:www\.)?/i, "")
                          .split(".")
                          .slice(0, -1)
                          .join(".") + " "}
                        {/* { site.replace(/^(?:https?:\/\/)?(?:www\.)?/i, "").split('/')[0] + " " } */}
                      </a>
                    ))}
                  </span>
                </p>
              </div>
            </div>
          </div>
          <div className="label-top-controls">
            <div className="pagination">
              {page}-{per_page} of {pagination.items}{" "}
              <a
                href="/sp/react/react-discogs-search"
                onClick={(e) => this.handlePrev(e, id)}
              >
                Prev
              </a>{" "}
              <a
                href="/sp/react/react-discogs-search"
                className="page-num-link"
              >
                {pagination.pages}{" "}
              </a>{" "}
              <a
                href="/sp/react/react-discogs-search"
                onClick={(e) => this.handleLoad(e, id)}
              >
                Next
              </a>
            </div>
            <div className="label-jumpto" />
            <div className="label-genre">
              Genre{" "}
              <select name="genre" id="genre" onChange={this.handleStyleChange}>
                <option value="All">All</option>
                <option value="Electronic">Electronic</option>
                <option value="Hip Hop">Hip Hop</option>
                <option value="Rock">Rock</option>
                <option value="Funk / Soul">Funk / Soul</option>
                <option value="Pop">Pop</option>
              </select>
            </div>
            <div className="label-show">
              Show{" "}
              <select name="genre" id="genre" onChange={this.handleShowNumChange}>
                <option value="25">25</option>
                <option value="50">50</option>
                <option value="100">100</option>
                <option value="250">250</option>
                <option value="500">500</option>
              </select>
            </div>
            <div className="label-controls">
              {/* <a className="inset_button needs_tooltip" data-placement="top" data-size="big" title="" href="#" aria-label="Large Covers" data-original-title="Large Covers">
                            <i className="icon icon-th-large"></i>
                        </a>
                        <a className="inset_button needs_tooltip" data-placement="top" data-size="med" title="" href="#" disabled="disabled" aria-label="Text With Covers" data-original-title="Text With Covers">
                            <i className="icon icon-list"></i>
                        </a>
                        <a className="inset_button needs_tooltip" data-placement="top" data-size="sm" title="" href="#" aria-label="Text Only" data-original-title="Text Only">
                            <i className="icon icon-align-justify"></i>
                  </a> */}
            </div>
          </div>
          <div className="label-releases">
            <table style={{ width: "100%" }}>
              <tbody>
                <tr>
                  <th>Artist</th>
                  <th>Title(Format)</th>
                  <th>Cat#</th>
                  <th>Year</th>
                </tr>
                {releases.slice(start_index, visible).map((release, index) => (
                  <tr key={index}>
                    <td>
                      <Link
                        to={{
                          pathname: `/sp/react/react-discogs-search/artist/${release.id}`, state: { result }
                        }}
                      >
                        <img src={release.thumb} alt="thumbnail" className="label-thumbnail" /> {release.artist}
                      </Link>
                    </td>
                    <td>
                      <Link
                        to={{
                          pathname: `/sp/react/react-discogs-search/release/${
                            release.id
                          }`,
                          state: { result }
                        }}
                      >
                        {release.title}
                      </Link>
                    </td>
                    <td>{release.catno}</td>
                    <td>{release.year}</td>
                  </tr>
                ))}
              </tbody>
            </table>
            <div className="pagination">
              {page}-{per_page} of {pagination.items}{" "}
              <a
                href="/sp/react/react-discogs-search"
                onClick={this.handlePrev}
              >
                Prev
              </a>{" "}
              <a
                href="/sp/react/react-discogs-search"
                className="page-num-link"
              >
                {pagination.pages}{" "}
              </a>{" "}
              <a
                href="/sp/react/react-discogs-search"
                onClick={(e) => this.handleLoad(e, id)}
              >
                Next
              </a>
            </div>
          </div>
        </div>
        <Footer footer={this.state.footer} />
        <Modal
          images={images}
          currentFull={this.state.currentFull}
          currentThumb={this.state.currentThumb}
          handlePrev={this.handlePrev}
          handleNext={this.handleNext}
          handleModalImg={this.handleModalImg}        
          result={result}
          show={this.state.show}
          hideModal={this.hideModal}
        />
      </React.Fragment>
    );
    return (
      <React.Fragment>
        <div className="logo logo-search label">
          <img src={logo} alt="logo" />
        </div>
        {labelContent}
      </React.Fragment>
    );
  }
}
export default Label;
