import React, { Component } from "react";
import logo from "../logo.svg";
import Footer from "./Footer";

class Master extends Component {
  constructor(props) {
    super(props);
    this.state = {
      result: this.props,
      tracklist: [],
      versions: [],
      num_for_sale: Number
    };
    console.log(this.props);
  }

  handleClick = e => {
    e.preventDefault();
    const elem = document.querySelectorAll(".tracklist-item");
    [].forEach.call(elem, el => {
      el.classList.toggle('collapse');
      if (el.classList.contains('collapse')) {
        this.setState({ creditsState: 'collapse' });
      }
    });
    sessionStorage.setItem('creditsState', this.state.creditsState);
  };

  componentDidMount = () => {
    const { id } = this.props.match.params;
    Promise.all([
      fetch(`https://api.discogs.com/masters/${id}`),
      fetch(`https://api.discogs.com/masters/${id}/versions`),
    ])
      .then(([res1, res2]) => Promise.all([res1.json(), res2.json()]))
      .then(([data1, data2]) => {
        console.log(data1, data2);
        this.setState({
            num_for_sale: data1.num_for_sale,
            tracklist: data1.tracklist,
            versions: data2.versions,
          }, () => localStorage.setItem('tracklist', JSON.stringify(data1.tracklist))
        );
      })
      .catch(err => console.log(err));
  };

  render() {
    let masterContent;
    const { id } = this.props.match.params;
    const { result } = this.props.location.state;
    const { tracklist, versions, num_for_sale } = this.state;

    if(tracklist === undefined || tracklist.length === 0) {
        return(
            <div className="loading-container">
                Loading...
            </div>
        )
    }

    masterContent = (
      <React.Fragment>
        <div className="logo logo-search master">
          <img src={logo} alt="logo" />
        </div>
        <div className="container master">
          <div className="master-left">
            <div className="master-top">
              <div className="master-top-left">
                <div className="master-image">
                  <a href={`/artist/${id}`}>
                    <img src={result.cover_image} alt={result.title} />
                  </a>
                </div>
                <div className="master-image-link">
                  <a href={`/artist/${id}`}>More Images</a>
                </div>
              </div>
              <div className="master-top-center-artist-desc">
                <div className="master-title">
                  <p>{result.title}</p>
                </div>
                <div className="master-genre">
                  <p>Genre: {result.genre}</p>
                </div>
                <div className="master-style">
                  <p>Style: {result.style}</p>
                </div>
                <div className="master-year">
                  <p>Year: {result.year}</p>
                </div>
              </div>
            </div>
            <div className="master-bottom">
              <div className="tracklist">
                <div className="tracklist-top">
                  <p>Tracklist</p>
                </div>
                {tracklist.map((track, index) => (
                  <div key={index} className="tracklist-item">
                    <p>
                      {track.position} {track.title}{" "}
                      <span className="track-duration">{track.duration}</span>
                    </p>
                  </div>
                ))}
              </div>
              <div className="master-versions">
                <div className="version-top">Versions({versions.length})</div>
                <div className="versions">
                  <React.Fragment>
                    <table style={{ width: "100%" }}>
                      <tbody>
                        <tr>
                          <th>Title(Format)</th>
                          <th>Label</th>
                          <th>Cat#</th>
                          <th>Country</th>
                          <th>Year</th>
                        </tr>
                        {versions.map((version, index) => (
                          <tr key={index}>
                            <td>
                              <div className="version-item">
                              <img src={result.thumb} style={{width:'50px'}} alt={'thumbnail'} />
                                <p>
                                  <a href="#!">{version.title}</a> 
                                    {' '}
                                   ({version.format})
                                </p>
                              </div>
                            </td>
                            <td>
                              <div className="version-item">
                                <p>
                                  <a href="#!">{version.label}</a>
                                </p>
                              </div>
                            </td>
                            <td>
                              <div className="version-item">
                                <p>{version.catno}</p>
                              </div>
                            </td>
                            <td>
                              <div className="version-item">
                                <p>{version.country}</p>
                              </div>
                            </td>
                            <td>
                              <div className="version-item">
                                <p>{result.year}</p>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </React.Fragment>
                </div>
              </div>
            </div>
          </div> 
          <div className="master-right">
            <div className="master-top">
                <p>Master Release</p>
            </div>
            <div className="marketplace-header">
                <p>Marketplace <span className="marletplace-num-for-sale"><a href="#!">{num_for_sale} For sale</a></span></p>
            </div>
            <div className="statistics-header">
                <p>Statistics</p>
            </div>
            <div className="statistics">
                <table style={{width: '100%'}}>
                    <tbody>
                        <tr>
                            <td>Have: {result.community.have}</td>
                            <td>Avg. Rating: </td>
                        </tr>
                        <tr>
                            <td>Want: {result.community.want}</td>
                            <td>Ratings: </td>
                        </tr>
                    </tbody>
                </table>
            </div>
          </div>
        </div>
        <Footer />
      </React.Fragment>
    );

    return <React.Fragment>{masterContent}</React.Fragment>;
  }
}
export default Master;
