import React, { Component } from 'react';
import logo from '../logo.svg';
import Footer from './Footer';
import ToggleCredits from './ToggleCredits';
import Modal from './Modal';

class Release extends Component {
    constructor(props) {
        super(props);
        this.state = {
            creditText: 'Hide Credits',
            creditsState: 'collapse',
            result: this.props,
            show: false,
            footer: "secondary",
            currentFull: "current-full",
            currentThumb: "current-thumb",
            images: [],
            tracklist: [],
            extraartists: [],
            extraartistsTrack: [],
            toggleCreditText: 'Show Credits'
        }
        // console.log(this.props);
    }

    showModal = e => {
        console.log('showModal Ran');
        e.preventDefault();
        this.setState({ show: true });
        let currentFull = document.querySelector(".current-full");
        let currentThumb = document.querySelector(".current-thumb");
        let prevSlideBtn = document.querySelector(".arrow-left");
    
        let thumbnail_data_id = document.querySelector(".full").dataset.id;
        let hostname = window.location.host;
        let pathname = window.location.pathname;
        let newUrl = hostname + pathname;
    
        let permalinkUrl = `${newUrl}#images/${thumbnail_data_id}`;
    
        const permalinkInput = document.querySelector(".image_copy_permalink_text");
    
        // Populate input with value of each date-id value
        permalinkInput.value = permalinkUrl;
    
        if (
          !currentFull.previousElementSibling &&
          !currentThumb.previousElementSibling
        ) {
          prevSlideBtn.style.display = "none";
        }
    
        // if images in modal are greater than 3 need to adjust width of slideshow_wrapper to 400% 
        //and individual slides to 25.0000%
        const { images } = this.state;
        console.log('images', images);
      };

      hideModal = e => {
        e.preventDefault();
        this.setState({ show: false });
      };

      handlePrev = e => {
        e.preventDefault();
        let currentFull = document.querySelector(".current-full");
        let currentThumb = document.querySelector(".current-thumb");
        let full = document.querySelectorAll(".full");
        let thumbs = document.querySelectorAll(".thumb");
        let prevSlideBtn = document.querySelector(".arrow-left");
        let nextSlideBtn = document.querySelector(".arrow-right");
    
        let image_gallery_slide_wrapper = document.querySelector(
          ".image_gallery_slide_wrapper"
        );
    
        currentFull.classList.remove("current-full");
        currentThumb.classList.remove("current-thumb");
    
        if (
          !currentFull.previousElementSibling.previousElementSibling &&
          !currentThumb.previousElementSibling.previousElementSibling
        ) {
          prevSlideBtn.style.display = "none";
        }
    
        if (
          currentFull.previousElementSibling &&
          currentThumb.previousElementSibling
        ) {
          currentFull.previousElementSibling.classList.add("current-full");
          currentThumb.previousElementSibling.classList.add("current-thumb");
    
          nextSlideBtn.style.display = "block";
    
          // move slideshow image to the prev
          console.log(image_gallery_slide_wrapper);
    
          // In order to animate the property values, the string needs to be converted to a number
          // style.right is a string
          image_gallery_slide_wrapper.style.left =
            parseInt(image_gallery_slide_wrapper.style.left, 10) + 100 + "%";
          console.log(image_gallery_slide_wrapper.style.left);
        } else {
          full[thumbs.length - 1].classList.add("current-full");
          thumbs[thumbs.length - 1].classList.add("current-thumb");
        }
      };

      handleNext = e => {
        e.preventDefault();
        let currentFull = document.querySelector(".current-full");
        let currentThumb = document.querySelector(".current-thumb");
        let full = document.querySelectorAll(".full");
        let thumbs = document.querySelectorAll(".thumb");
        let prevSlideBtn = document.querySelector(".arrow-left");
        let nextSlideBtn = document.querySelector(".arrow-right");
    
        currentFull.classList.remove("current-full");
        currentThumb.classList.remove("current-thumb");
    
        if (
          !currentFull.nextElementSibling.nextElementSibling &&
          !currentThumb.nextElementSibling.nextElementSibling
        ) {
          nextSlideBtn.style.display = "none";
        }
    
        if (currentFull.nextElementSibling && currentThumb.nextElementSibling) {
          currentFull.nextElementSibling.classList.add("current-full");
          currentThumb.nextElementSibling.classList.add("current-thumb");
          // move slideshow image to the next
          let image_gallery_slide_wrapper = document.querySelector(
            ".image_gallery_slide_wrapper"
          );
          console.log(image_gallery_slide_wrapper);
          prevSlideBtn.style.display = "block";
    
          // In order to animate the property values, the string needs to be converted to a number
          // style.left is a string
          image_gallery_slide_wrapper.style.left =
            parseInt(image_gallery_slide_wrapper.style.left, 10) - 100 + "%";
          console.log(image_gallery_slide_wrapper.style.left);
        } else {
          full[0].classList.add("current-full");
          thumbs[0].classList.add("current-thumb");
        }
      };

    handleClick = (e) => {
        e.preventDefault();

        this.setState({ toggleCreditText: 'Hide Credits' });  

        const elem = document.querySelectorAll('.tracklist-item');
        [].forEach.call(elem, (el) => {
            if(el.classList.contains('collapse')){                
                el.classList.remove('collapse');
                sessionStorage.removeItem('creditsState');
                this.setState({creditText: 'Show Credits'}, () => {
                    sessionStorage.removeItem('creditText');
                    // sessionStorage.setItem('creditText', JSON.stringify(this.state.creditText));
                });
            } else {
                el.classList.add("collapse");
                this.setState({ creditsState: 'collapse', creditText: 'Hide Credits' }, () => {
                    sessionStorage.setItem('creditText', JSON.stringify(this.state.creditText));
                    sessionStorage.setItem('creditsState', JSON.stringify(this.state.creditsState));
                });                   
            }
        });   
    }

    componentDidMount = () => {
        const {id} = this.props.match.params;
        fetch(`https://api.discogs.com/releases/${id}?token=${process.env.REACT_APP_API_KEY}`)
        .then(res => res.json())
        .then(data => {
            console.log('data from release fetch', data);
                this.setState({
                    images: data.images,
                    tracklist: data.tracklist,
                    extraartists: data.extraartists,
                    extraartistsTrack: data.tracklist[0].extraartists
                }, () => localStorage.setItem('tracklist', JSON.stringify(data.tracklist)));
        });
    }

    render(){
        let releaseContent;
        const { id } = this.props.match.params;
        const { result } = this.props.location.state;
        const { tracklist, extraartists, extraartistsTrack, images } = this.state;   
        
        if(tracklist === undefined || tracklist.length === 0) {
            return(
                <div className="loading-container">
                    Loading...
                </div>
            )
        }

        releaseContent = (
             <React.Fragment>
                <div className="logo logo-search release">
                    <img src={logo} alt="logo" />
                </div>                                
                <div className="container release">
                <div className="release">
                    <div className="release-top">
                        <div className="release-top-left">
                            <div className='release-image'>
                                <a href={`/artist/${id}`} onClick={this.showModal}>
                                    <img src={result.cover_image} alt={result.title} />
                                </a>
                                <div className="release-image-link">
                                    <a href={`/artist/${id}`} onClick={this.showModal}>More Images</a>
                                </div>
                            </div>  
                        </div>
                        <div className="release-top-right">
                            <div className="release-title"><p>{result.title} </p></div>
                            <div className="release-label"><p>Label: {result.label[0]}</p></div>
                            <div className="release-format"><p>Format: {result.format[0]+', '+result.format[1]}</p></div>
                            <div className="release-country"><p>Country: {result.country}</p></div>
                            <div className="release-year"><p>Released: {result.year}</p></div>
                            <div className="release-genre"><p>Genre: {result.genre}</p></div>
                            <div className="release-style"><p>Style: {result.style[0] && result.style[1]  ? result.style[0]+', '+result.style[1] : ''}</p></div>
                        </div>
                        <div className="release-collection-status"></div>
                    </div>
                    <div className="release-bottom">
                        <div className="tracklist">
                             <div className="tracklist-top">
                                <p>Tracklist</p>     
                                <ToggleCredits
                                    toggleCreditText={this.state.toggleCreditText}
                                    handleClick={this.handleClick}
                                    extraartistsTrack={extraartistsTrack}
                                />
                             </div>
                             {tracklist.map((track, index) => (
                                <div key={index} className={`tracklist-item`+(sessionStorage.getItem("creditsState") ? ' collapse' : ' not true' )}>
                                    <p>{track.position ? track.position : ''} {track.title ? track.title : ''} <span className="track-duration">{track.duration !== '' ? track.duration : ''}</span></p>
                                    {track.extraartists ? track.extraartists.map((extraartist, index) => (
                                    <p key={index} className="extraartists">                                       
                                        {/* {extraartist.role ? extraartist.role + ' - ' + extraartist.name : ''} */}
                                        {/* {extraartist.role} - {extraartist.name} */}
                                    </p>
                                    )) : ''}
                                </div>
                             ))}
                        </div>
                        <div className="credits">
                             <div className="credits-top">
                                <p>Credits</p>
                             </div>
                             {extraartists.map((artist, index) => (
                                <div key={index} className="credits-item">
                                    <p>{artist.role} - {artist.name} (tracks: {artist.tracks})</p>
                                    {/* {track.extraartists.map((extraartist, index) => (
                                    <p key={index} className="extraartists">
                                        {extraartist.role}
                                    </p>
                                    ))} */}
                                </div>
                             ))}
                        </div>
                    </div>
                </div>
                </div>
                <Footer footer={this.state.footer} />
                <Modal
                    images={images}
                    currentFull={this.state.currentFull}
                    currentThumb={this.state.currentThumb}
                    handlePrev={this.handlePrev}
                    handleNext={this.handleNext}
                    handleModalImg={this.handleModalImg}
                    result={result}
                    show={this.state.show}
                    hideModal={this.hideModal}
                    handlePermalink={this.handlePermalink}
                />
             </React.Fragment>
        );
        return(
            <React.Fragment>
                {releaseContent}
            </React.Fragment>
        )
    }
}
export default Release;