import React, { Component } from 'react';
import PropTypes from "prop-types";

class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            footer: this.props
        }
    }
    render() {
        return (
            <footer className={`container ${this.props.footer}`}>
                <p>React Discogs Search | 2019</p>
            </footer>
        );
    }
}

Footer.propTypes = {
    footer: PropTypes.string
}

export default Footer;