import React, { Component } from "react";
import { Spring } from "react-spring/renderprops";
import SearchDropdown from "./SearchDropdown";
import Spinner from "./Spinner";
import SearchButton from "./SearchButton";
import { debounce } from "lodash";

class SearchBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      prefetch: <SearchButton handleClick={this.handleClick} />,
      search_term: '',
      results: []
    };
  }

  handlePreFetch = async () => {
    await this.setState({
      prefetch: <Spinner />
    });
  };

  handleClick = e => {
    e.preventDefault();
  };

  // Use debounce technique to handle API requests
  handleSearch = debounce(async () => {
    let search_term = document.querySelector(".search-term").value;

    await fetch(
      `https://api.discogs.com/database/search?q=${search_term}&token=${
        process.env.REACT_APP_API_KEY
      }`
    )
      .then(res => res.json())
      .then(data => {
        this.setState(
          {
            search_term: search_term,
            results: data.results,
            prefetch: <SearchButton />
          },
          () => console.log(this.state)
        );
      })
      .catch(err => console.error(err));
  }, 500);

  render() {
    const { prefetch } = this.state;
    const { results } = this.state;
    const { search_term } = this.state;

    return (
      <React.Fragment>
        <div className="search-bar">
          <form action="/Search">
            <Spring
              from={{ opacity: 0 }}
              to={{ opacity: 1 }}
              config={{ delay: 2200, duration: 1000 }}
            >
              {props => (
                <React.Fragment>
                  <p className="example-text">eg. kut klose</p>
                  <input
                    type="text"
                    className="search-bar-input search-term"
                    placeholder="Search Discogs Database"
                    onKeyUp={this.handlePreFetch.bind(this)}
                    onChange={this.handleSearch.bind(this)}
                  />
                  <div className="search-status">{prefetch}</div>
                </React.Fragment>
              )}
            </Spring>
          </form>
        </div>
        <SearchDropdown search_term={search_term} results={results} />
      </React.Fragment>
    );
  }
}

export default SearchBar;
