import React, { Component } from "react";
import PropTypes from "prop-types";
import uuid from 'uuid';

export default class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: this.props.show,
      result: this.props.result,
      currentFull: this.props.currentFull,
      currentThumb: this.props.currentThumb,
      images: this.props.images
    };
  }

  componentDidMount() {
    let image_gallery_slide_wrapper = document.querySelector('.image_gallery_slide_wrapper');
    image_gallery_slide_wrapper.style.left = "0%";
  }

  render() {
    const { images } = this.props;

    return (
      <React.Fragment>
        <div className={`image_gallery_modal ${this.props.show}`}>
            <div className="image_gallery_actions">
              <button className="image_zoom button button-small hide_mobile">
                <span className="image_zoom_in">
                  <i className="fas fa-search-plus" /> Zoom In
                </span>
                <span className="image_zoom_out">
                  <i className="fas fa-search-minus" /> Zoom Out
                </span>
              </button>
              <button
                className="image_copy_permalink button button-small hide_mobile"
                onClick={this.props.handlePermalink}
              >
                <i className="fas fa-link" /> Permalink
              </button>
              <input className="image_copy_permalink_text" type="text" />
              <a
                href="/artist/174381-Kut-Klose/images/update"
                className="images_edit button button-small"
              >
                <i className="fas fa-edit" /> Edit Images
              </a>
            
            <div className="modal-close-btn">
              <a href={`close-btn`} onClick={this.props.hideModal}>
                <i className="fas fa-times" />
              </a>
            </div>            
          </div>          
            <a className="arrow-left image_gallery_nav image_gallery_prev hide_mobile" href="#!" onClick={this.props.handlePrev}>
              <i
                className="fas fa-chevron-left"                
              />
            </a>
            <a className="arrow-right image_gallery_nav image_gallery_next" href="#!" onClick={this.props.handleNext}>
              <i
                className="fas fa-chevron-right"                
              />
            </a>
            <div onClick={this.props.hideModal} className={`image_gallery_slide_wrapper ${this.props.show}`} style={ images.length === 2 ? { width: '300%' } : { width: '400%' } }>
              {images.map((image, index) => (
                <div
                  style={images.length === 2 ? { width: '33.3333%' } : { width: '25.0000%' }}
                  data-id={uuid.v4()}
                  key={index}
                  className={`full image_gallery_slide ${
                    index === 0 ? index + " " + this.state.currentFull : index
                  }`}
                >
                  <img className="loaded" src={image.uri} alt="uri" />
                </div>
              ))}
            </div>
            <div className={`modal-thumbnails ${this.props.show}`}>
            {images.map((image, index) => (
              <div
                key={index}
                className={`thumb ${
                  index === 0 ? index + " " + this.state.currentThumb : index
                }`}
              >
                <a href="#!" onClick={this.props.handleModalImg}>
                  <img
                    src={image.uri}
                    height={image.height}
                    width={image.width}
                    alt="uri"
                  />
                </a>
              </div>
            ))}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

Modal.propTypes = {
  images: PropTypes.array,
  show: PropTypes.bool,
  result: PropTypes.object,
  hideModal: PropTypes.func,
  currentFull: PropTypes.string,
  currentThumb: PropTypes.string,
  handlePermalink: PropTypes.func,
  handlePrev: PropTypes.func,
  handleNext: PropTypes.func
};
