import React from 'react'
import { Spring } from 'react-spring/renderprops';

function Logo() {
  return (
    <React.Fragment>
        <Spring
          from={{ opacity: 0, marginTop: -50 }}
          to={{ opacity: 1, marginTop: 0 }}
          config={{ delay: 1000, duration: 1000 }}
        >
        {props => (
        <div style={props}>
            <img src={require('../logo.svg')} alt="logo" />
        </div>
        )}
        </Spring>
    </React.Fragment>
  )
}
export default Logo;