// Pass props to component and use lifecycle method to try and update toggle text on the fly
import React, { Component } from "react";

class ToggleCredits extends Component {
  
  componentDidUpdate(prevProps) {
    
    console.log(prevProps);
    if(this.props.handleClick) {
    }
  }
  
  render() {
    return (
      <div>
        {this.props.extraartistsTrack ? (
          <p>
            <a
              href="/"
              className={`credits-toggle`}
              onClick={this.props.handleClick.bind(this)}
            >
              {sessionStorage.getItem("creditText") !== null
                ? "Hide Credits"
                : "Show Credits"}
            </a>
          </p>
        ) : (
          ""
        )}
      </div>
    );
  }
}

export default ToggleCredits;
