import React from 'react'
import { Spring } from 'react-spring/renderprops';

export default function IntroText() {
  return (
       <Spring
          from={{ opacity: 0 }}
          to={{ opacity: 1 }}
          config={{ delay: 2200, duration: 1000 }}
        >
        {props => (
        <div style={props}>
          <p>This project is built in Reactjs. A Front-End UI Library.</p>
        </div>
        )}
        </Spring>
  )
}
